.footer {
  box-sizing: border-box;
  flex: 0 0 auto;
}

.footer a {
  text-decoration: none;
}

.footer__wrapper {
  display: flex;
  padding: 65px 25px 42px;
  margin: 0 auto;
  max-width: 1409px;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 20px;
  }

  @media (max-width: $mobile-width) {
    display: flex;
    flex-direction: column;
    margin-bottom: 0;
  }
}

.footer__logo {
  display: flex;
}

.footer__logo h2 {
  margin: 0;
  font-family: "RobotoCondesed", sans-serif;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #203c42;
}

.footer__logo p {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
  margin: 0;
}

.footer__logo div {
  padding: 7px 0 0 14px;
}

.footer__list-social {
  list-style: none;
  margin: 54px 0 0;
  padding: 0;
  display: flex;

  @media (max-width: $tablet-width) {
    margin: 0;
    padding-top: 4px;
  }
}

.footer__list-social li {
  margin-right: 15px;
}

.footer__list-social li:last-child {
  margin-right: 0;
}

.footer__sections {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: 39px;
  padding-left: 30px;

  @media (max-width: $tablet-width) {
    padding-left: 24px;
    padding-top: 38px;
    padding-right: 15px;
    margin-left: 0;
    margin-right: 0;
  }

  @media (max-width: $mobile-width) {
    padding-left: 12px;
    padding-right: 20px;
    padding-top: 0;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2);
  }
}

.footer__sections h2 {
  text-align: left;
  font-family: "RobotoCondesed", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1b2d37;
  margin: 0 0 0 3px;
  padding: 0;

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.footer__list-sections {
  min-height: 100px;
  display: flex;
  list-style: none;
  margin-top: 25px;
  padding: 0;
  flex-flow: row wrap;
  max-width: 620px;

  @media (max-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr;
    min-height: 175px;
    height: auto;
    margin-top: 26px;
  }

  @media (max-width: $mobile-width) {
    display: none;
    margin-top: 4px;
    margin-left: 3px;
    margin-bottom: 26px;
  }
}

.footer__list-sections a {
  font-family: "PT Mono", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  color: #1b2d37;
  opacity: 0.5;

  &:hover {
    opacity: 1;
  }
}

.footer__list-sections li {
  width: 50%;
  white-space: nowrap;
}


.footer__list-sections li:nth-child(2n + 1) {
  width: 38%;
}

.footer__list-sections li:nth-child(1n + 1) {
  margin-right: 75px;
}

.footer__office {
  @media (max-width: $tablet-width) {
    margin-left: 0;
    padding: 38px 0 0 16px;
  }

  @media (max-width: $mobile-width) {
    padding: 0 20px 0 12px;
  }
}

.footer__office h2 {
  text-align: left;
  font-family: "RobotoCondesed", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1b2d37;
  margin: 0 0 0 3px;
  padding: 0;

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.footer__list-office {
  list-style: none;
  margin-top: 25px;
  padding: 0;

  @media (max-width: $tablet-width) {
    margin-top: 25px;
  }

  @media (max-width: $mobile-width) {
    display: none;
    margin-top: 4px;
    margin-left: 3px;
    margin-bottom: 26px;
  }
}

.footer__list-office a {
  font-family: "PT Mono", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  color: #1b2d37;
  opacity: 0.5;

  &:hover {
    opacity: 1;
  }
}

.footer__list-office li {
  white-space: nowrap;
}

.footer__list-office li:first-child {
  white-space: normal;
}

.footer__paragraph {
  font-family: "PT Mono", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #1b2d37;
  opacity: 0.5;
  margin: 0;
}

.footer__right {
  display: flex;
  align-items: center;
  border-top: 1px solid rgba(98, 105, 98, 0.2);
  height: 52px;

  @media (max-width: $tablet-width) {
    padding: 7px 26px 11px 27px;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    padding-top: 28px;
  }
}

.footer__paragraph--first {
  margin-right: 74px;
  margin-left: 13.1%;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.footer__paragraph--second {
  margin-right: 7.5%;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.footer__paragraph--fourth {
  margin-left: auto;
  margin-right: 13.3%;

  @media (max-width: $tablet-width) {
    margin-right: 0;
  }

  @media (max-width: $mobile-width) {
    padding-top: 21px;
    padding-bottom: 25px;
    margin-left: 0;
  }
}

.footer__paragraph--tablet {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
    margin: 0 auto;
    padding-left: 15px;
    padding-top: 8px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 26px;
    padding-left: 0;
    padding-bottom: 27px;
  }
}

.footer__first {
  @media (max-width: $tablet-width) {
    display: flex;
    grid-column: 1 / 3;
    align-items: center;
    padding: 18px 25px 26px 26px;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2);
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    padding-top: 45px;
    padding-bottom: 50px;
  }
}

//sections

.footer__list-sections li:nth-child(10n + 3) {
  @media (max-width: $tablet-width) {
    grid-row: 2;
  }
}

.footer__list-sections li:nth-child(11n + 5) {
  @media (max-width: $tablet-width) {
    grid-row: 3;
  }
}

.footer__list-sections li:nth-child(2n + 2) {
  margin-right: 0;
  width: 139px;
}

.footer__list-sections li:last-child {
  @media (max-width: $tablet-width) {
    grid-row: 4;
  }
}

//button

.button--no-js .footer__list-sections--show {
  @media (max-width: $mobile-width) {
    display: block;
  }
}

.footer__list-sections--show {
  display: grid;
}

.button--no-js .footer__list-office--show {
  @media (max-width: $tablet-width) {
    display: block;
  }
}

.footer__list-office--show {
  display: block;
}
